import React from 'react';
import {Link} from 'react-router-dom';
import './listing.css';
import Pagination from 'react-js-pagination'


const renderRest = (props,data) => {
    if(data){
        if(data.length>0){
            var limit=props.limit;
            var page = props.activePage;
            data = data.slice((page-1)*limit,(page-1)*limit+limit)
            return data.map((item) =>{
            return(
                <Link to={`/rest/${item._id}`}>
                    <div className="Item" id={item.id}>
                        <div className="row">
                            <div className="col-sm-4 col-md-4 col-lg-4">
                                <img className="Image" src={item.thumb}/>
                            </div>
                            <div className="col-sm-8 col-md-8 col-lg-8">
                                <div className="rest_name">
                                    {item.name}
                                    <div className="city_name">{item.city_name}</div>
                                    <div className="address-text">{item.locality}</div>
                                    <div className="address-text">{item.address}</div> 
                                </div>
                            </div>
                        </div>
                        <br/>
                        <hr/>

                        <div className="col-sm-6 col-md-3 col-lg-3">
                            <div class="CUISINES-COST-FOR-TWO">Cuisine Type</div>
                            <div class="CUISINES-COST-FOR-TWO">Cost For Two</div>
                        </div>
                        <div className="col-sm-6 col-md-9 col-lg-9">
                            <div class="Bakery-700">{item.Cuisine[0].name},{item.Cuisine[1].name}</div>
                            <div class="Bakery-700">Rs {item.cost}</div>
                        </div>
                    </div>
                </Link>
            )

        })
        }else{
            return(
                <div>
                    <center>
                        <h2>No Data For This Filter</h2>
                    </center>
                </div>
            )
        }
       
    }else{
        return(
            <div>
                <img src="/images/loader.gif"/>
            </div>
        )
    }
}

const handlePageChange = (props,pageNumber) =>{
    props.pageNumber(pageNumber);
    var data=props.restdata;
    renderRest(props,data)
}

const  ListingDisplay = (props) =>{
     return(
        <div className="container">
            <div className="main-heading">
                <div className="row">
                    <div className="col-sm-12 col-md-12 col-lg-12">
                        {renderRest(props,props.restdata)}
                    </div>
                </div>
            </div>
            <center>
                <Pagination
                    activePage={props.activePage}
                    itemsCountPerPage={2}
                    totalItemsCount={props.totalNoOfItems}
                    pageRangeDisplayed={5}
                    itemClass="page-item"
                    linkClass="page-link"
                    onChange={(pageNumber)=>{handlePageChange(props,pageNumber)}}
                />
            </center>
        </div>
    )
}

export default ListingDisplay;