import React from 'react';
import './quickSearch.css';
import {Link} from 'react-router-dom';


const QuickSearch = (props) =>{

const listMeals = ({mealData}) =>{
    if(mealData){
        return mealData.map((item)=>{
            return(
                <Link to={`/list/${item.mealtype}`}>
                    <div className="tileContainer">
                                <div className="tileComponent1">
                                    <img src={`./images/${item.name}.png`} className="blockImage"/>
                                </div>
                                <div className="tileComponet2">
                                    <div className="componentHeading">
                                        {item.name}
                                    </div>
                                    <div className="componentSubHeading">
                                        Start your day with exclusive breakfast
                                    </div>
                                </div>
                            </div>
                </Link>
                
                
            )
        })
        
    }
}
    return(
        <div className="quickSearchContainer">
                <p className="quickSearchHeading">
                    Quick Searches
                </p>
                <p className="quichSearchSubHeading">
                    Discover Restaurants by meal type
                </p>
                <div>
                    {listMeals(props)}
                </div>
                
    </div>
    )
}

export default QuickSearch;