import React,{Component} from 'react';
import axios from 'axios';
import '../filters/filter.css';

const lurl="https://ajrestaurantapp.herokuapp.com/location";
const url="https://ajrestaurantapp.herokuapp.com/restaurantList";

class SearchFilter extends Component{
    constructor(){
        super()

        this.state={
            location:''
        }
    }

    
    renderCity=(data)=>{
        if(data){
            return data.map((item) => {
                return(
                    <option value={item.city}>
                       {item.city_name}
                    </option>
                )
            })
        }
    }

    searchLogic = (event) => {
        let mealtype = sessionStorage.getItem('type');
        let place = event.target.value;
        let curl;
        if(place==""){
            curl = `${url}/${mealtype}`
        }else{
            curl = `${url}/${mealtype}?city=${event.target.value}`
        }

        axios.get(curl)
        .then((response) => {this.props.restpersearch(response.data)})
        
        console.log(curl)

    }


    render(){
        return(
            <div>
                <div className="Filters">Filters</div>
                <div className="Select-Location">Select Location</div>
                <div classNaame="city-select">
                <select className="select" 
                    onChange={this.searchLogic}>
                        <option style={{fontFamily:"Poppins"}}>Select City</option>
                        {this.renderCity(this.state.location)}
                    </select>
                </div>
               
            </div>
        )
    }

    componentDidMount(){
        fetch(lurl,{method:'GET'})
        .then((res)=>res.json())
        .then((data)=>{
            this.setState({location:data})
        })
       
    }
}

export default SearchFilter;