import React,{Component} from 'react';
import ListingDisplay from './ListingDisplay';
import CuisineFilter from '../filters/cuisineFilter';
import CostFilter from '../filters/costFilter';
import SearchFilter from '../filters/searchFilter';
import SortFilter from '../filters/sortFilter';
import '../filters/filter.css';



const lurl="https://ajrestaurantapp.herokuapp.com/restaurant?mealtype=";
const limit = 2;

class Listing extends Component{
    constructor(props){
        super(props)


        this.state={
            restListing:'',
            activePage:1,
            totalNoOfItems:1
        }
    }

    setDataPerFilter(sortedData){
        this.setState({restListing:sortedData})
    }

    render(){
        console.log(this.props.match.params.id)
        return(
            <div>
                <div className="row">
                    <div className="col-sm-3 col-md-3 col-lg-3">
                        <div className="filter-search">
                            <SearchFilter restpersearch={(data) => {this.setDataPerFilter(data)}}/>     
                            <CuisineFilter restpercuisine={(data) => {this.setDataPerFilter(data)}}/>
                            <CostFilter restpercost={(data) => {this.setDataPerFilter(data)}}/>
                            <SortFilter restpersort={(data) => {this.setDataPerFilter(data)}}/>
                        </div>
                    </div>
                    <div className="col-sm-9 col-md-9 col-lg-9">
                        <ListingDisplay restdata={this.state.restListing} activePage={this.state.activePage}
                        limit={limit} totalNoOfItems={this.state.totalNoOfItems} pageNumber={(data)=>{this.setState({activePage:data})}}/>
                    </div>
                </div>
            </div>
        )
    }

    componentDidMount(){
        var mealId=this.props.match.params.id;
        sessionStorage.setItem('type',mealId);
        fetch(`${lurl}${mealId}`)
        .then((res)=>res.json())
        .then((data)=>{
            this.setState({
                restListing:data.slice(0, data.length-1),
                totalNoOfItems:data.length-1
            })
        })
        
    }
}

export default Listing;