import React,{Component} from 'react';
import axios from 'axios';
import '../filters/filter.css';

const url="https://ajrestaurantapp.herokuapp.com/restaurantList";

class SortFilter extends Component{

    sortLogic = (event) => {
        let mealtype = sessionStorage.getItem('type');
        let sorts = event.target.value;
        let curl;
        if(sorts==""){
            curl = `${url}/${mealtype}`
        }else{
            curl = `${url}/${mealtype}?sort=${event.target.value}`
        }

        axios.get(curl)
        .then((response) => {this.props.restpersort(response.data)})
        
        console.log(curl)

    }


    render(){
            return(
                <div>
                 <div class="radio-header">Sort</div>
                    <div onChange={this.sortLogic}>
                            <div class="block">
                                <input type="radio" class="inline-block" name="cost" value=""/>
                                <span class="inline-block checkbox-option">Price High to Low</span>
                            </div>
                            <div class="block">
                                <input type="radio" class="inline-block" name="cost" value="1"/>
                                <span class="inline-block checkbox-option">Price Low to High</span>
                            </div>
                    </div>
            </div>
            )
    }
}

export default SortFilter;